'use strict';

// MODULES //

var isnan = require( '@stdlib/math-base-assert-is-nan' );


// MAIN //

/**
* Returns the expected value for a Wald distribution with mean `mu` and shape parameter `lambda`.
*
* @param {PositiveNumber} mu - mean
* @param {PositiveNumber} lambda - shape parameter
* @returns {PositiveNumber} expected value
*
* @example
* var y = mean( 5.0, 2.0 );
* // returns 5.0
*
* @example
* var y = mean( 0.0, 1.0 );
* // returns NaN
*
* @example
* var y = mean( 1.0, 0.0 );
* // returns NaN
*
* @example
* var y = mean( NaN, 1.0 );
* // returns NaN
*
* @example
* var y = mean( 0.0, NaN );
* // returns NaN
*
* @example
* var y = mean( 0.0, 0.0 );
* // returns NaN
*/
function mean( mu, lambda ) {
	if (
		isnan( mu ) ||
		isnan( lambda ) ||
		lambda <= 0.0 ||
		mu <= 0.0
	) {
		return NaN;
	}
	return mu;
}


// EXPORTS //

module.exports = mean;
